/**
 * GeChiUI dependencies
 */
import { Button } from '@gechiui/components';
import { close } from '@gechiui/icons';
import { __experimentalLibrary as Library } from '@gechiui/block-editor';
import {
	useViewportMatch,
	__experimentalUseDialog as useDialog,
} from '@gechiui/compose';
import { useCallback } from '@gechiui/element';
import { useDispatch } from '@gechiui/data';

/**
 * Internal dependencies
 */
import useWidgetLibraryInsertionPoint from '../../hooks/use-widget-library-insertion-point';
import { store as editWidgetsStore } from '../../store';

export default function InserterSidebar() {
	const isMobileViewport = useViewportMatch( 'medium', '<' );
	const { rootClientId, insertionIndex } = useWidgetLibraryInsertionPoint();

	const { setIsInserterOpened } = useDispatch( editWidgetsStore );

	const closeInserter = useCallback( () => {
		return () => setIsInserterOpened( false );
	}, [ setIsInserterOpened ] );

	const [ inserterDialogRef, inserterDialogProps ] = useDialog( {
		onClose: closeInserter,
	} );

	return (
		<div
			ref={ inserterDialogRef }
			{ ...inserterDialogProps }
			className="edit-widgets-layout__inserter-panel"
		>
			<div className="edit-widgets-layout__inserter-panel-header">
				<Button icon={ close } onClick={ closeInserter } />
			</div>
			<div className="edit-widgets-layout__inserter-panel-content">
				<Library
					showInserterHelpPanel
					shouldFocusBlock={ isMobileViewport }
					rootClientId={ rootClientId }
					__experimentalInsertionIndex={ insertionIndex }
				/>
			</div>
		</div>
	);
}
